.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(0 0 0 / 50%);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background: white;
  padding: 20px;
  border-radius: 5px;
  width: 90%;
  height: 90%; /* Высота модального окна 90% */
  display: flex;
  flex-direction: column;
}

.placeholder-tabs {
  display: flex;
  margin-bottom: 10px;
  border-bottom: 1px solid #d0d0d0;
}

.placeholder-tab {
  padding: 10px 20px;
  border: none;
  background-color: #f1f8ff;
  cursor: pointer;
  transition: background-color 0.3s;
  margin-right: 4px;
  border-radius: 4px 4px 0 0;
}

.placeholder-tab.active {
  background-color: #1890ff;
  color: #fff;
}

.placeholder-tab:hover {
  background-color: #e0e0e0;
}

textarea {
  flex: 1; 
  margin-bottom: 10px;
  padding: 8px;
  border: 1px solid #d0d0d0;
  border-radius: 4px;
  box-sizing: border-box;
  resize: none; 
}

.modal-buttons {
  display: flex;
  justify-content: flex-end;
}

.modal-button {
  background: #1890ff;
  border-color: #1890ff;
  color: #fff;
  padding: 4px 15px;
  border-radius: 2px;
  cursor: pointer;
}

.modal-button:hover {
  background-color: #188fff82;
}

.mr10 {
  margin-right: 10px;
}

.modal-close {
  position: absolute;
  top: 10px;
  right: 10px;
  border: none;
  background: none;
  font-size: 20px;
  cursor: pointer;
}
