.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(0 0 0 / 50%);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background: white;
  padding: 20px;
  border-radius: 5px;
  width: 90%;
  height: 90%;
  display: flex;
  flex-direction: column;
}

.modal-inputds {
  display: flex;
  justify-content: space-between;
}

.modal-inputds input {
  width: 49%;
  margin-bottom: 10px;
}

.modal-close {
  position: absolute;
  top: 10px;
  right: 10px;
  border: none;
  background: none;
  font-size: 20px;
  cursor: pointer;
}

textarea {
  flex: 1; /* Занимает основное пространство */
  margin-bottom: 10px;
  padding: 8px;
  border: 1px solid #d0d0d0;
  border-radius: 4px;
  box-sizing: border-box;
  resize: none; /* Отключение изменения размера */
}

.modal-buttons {
  display: flex;
  justify-content: flex-end;
}

.modal-button {
  background: #1890ff;
  border-color: #1890ff;
  color: #fff;
  padding: 4px 15px;
  border-radius: 2px;
  cursor: pointer;
}

.modal-button:hover {
  background-color: #188fff82;
}

.mr10 {
  margin-right: 10px;
}
